











export default {
	data() {
        return {
			height:'640px',
            title: 'map',
            latitude: 39.909,
            longitude: 116.39742,
            covers: [{
                latitude: 0,
                longitude: 0,
                iconPath: '../../../static/location.png'
            }, {
                latitude: 0,
                longitude: 0,
                iconPath: '../../../static/location.png'
            }]
        }
    },
    methods: {

    },
	onShow() {
		var that=this
		uni.getLocation({
		type: 'wgs84',
		success: function (res) {
			that.latitude=res.latitude;
			that.longitude=res.longitude;
		}
	});
	},
	onLoad() {
		var that=this;
		uni.getSystemInfo({
			success: function (res) {
				that.height=res.screenHeight+'px';
				console.log(that.height);
			}
		});
	}
};
